footer {
    display: flex;
    flex-direction: column;
    padding: 5em;
    /* animation:bg-change 10s infinite; */
    gap: 10px;
    background-color: #1F1F1F;
    color: white;

    /* animation:br-change 10s infinite; */
}

.portfolio__footer-head {
  font-size: 50px;
  font-weight: 900;
  align-items: flex-start;

  -webkit-text-stroke: 1px #f69000; /* width and color */
  color: #f6900000;
  font-family: 'League Spartan', sans-serif;
  -webkit-animation: text-flicker-in-glow 4s linear infinite both;
  animation: text-flicker-in-glow 4s linear infinite both;
}

.portfolio__footer-content {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  gap: 20px;
}

footer h3 {
  font-weight: 300 !important;
}

@media only screen and (max-width: 800px) {
  footer {
    padding: 2em;
  }

  .portfolio__footer-head {
    font-size: 40px;
  }
  .portfolio__footer-content {
    flex-direction: column;

  }
}

@keyframes br-change {
	0%,100% {
		border-top: 2px solid #f69000;
	}
  50% {
    border-top: 2px solid #53b1b1;
  }
}

